import React, { useState } from 'react';
import { Calendar, momentLocalizer } from 'react-big-calendar';
import moment from 'moment';
import 'react-big-calendar/lib/css/react-big-calendar.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Modal, Button, Form } from 'react-bootstrap';

//     Calendar
const localizer = momentLocalizer(moment);

//    
const testEvents = [
  {
    title: ' ',
    start: new Date(2024, 7, 10, 10, 0),
    end: new Date(2024, 7, 10, 11, 0),
  },
  {
    title: ' ',
    start: new Date(2024, 7, 11, 14, 0),
    end: new Date(2024, 7, 11, 15, 0),
  },
  {
    title: ' ',
    start: new Date(2024, 7, 12, 9, 0),
    end: new Date(2024, 7, 12, 10, 0),
  },
];

const MyCalendar = () => {
  //       
  const [events, setEvents] = useState(testEvents);
  const [showAddModal, setShowAddModal] = useState(false);
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [selectedEvent, setSelectedEvent] = useState(null);
  const [newEvent, setNewEvent] = useState({ title: '', start: '', end: '' });
  const [selectedEventToDelete, setSelectedEventToDelete] = useState(null);

  //    (       )
  const handleSelectSlot = ({ start, end }) => {
    setNewEvent({ start, end, title: '' });
    setShowAddModal(true); //      
  };

  //    (     )
  const handleSelectEvent = (event) => {
    setSelectedEventToDelete(event);
    setShowDeleteModal(true); //      
  };

  //     
  const handleAddEvent = () => {
    setEvents([...events, { ...newEvent, title: newEvent.title }]);
    setShowAddModal(false); //      
  };

  //     
  const handleDeleteEvent = () => {
    setEvents(events.filter((e) => e !== selectedEventToDelete));
    setShowDeleteModal(false); //      
  };

  return (
    <div className="container" style={{ padding: '20px' }}>
      <h2> </h2>
      <div className="row">
        <div className="col-md-12" style={{ height: '600px' }}>
          <Calendar
            localizer={localizer}
            events={events}
            startAccessor="start"
            endAccessor="end"
            onSelectSlot={handleSelectSlot}
            onSelectEvent={handleSelectEvent}
            selectable
            style={{ height: '100%' }}
          />
        </div>
      </div>

      {/*      */}
      <Modal show={showAddModal} onHide={() => setShowAddModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title> </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group controlId="formEventTitle">
              <Form.Label> </Form.Label>
              <Form.Control
                type="text"
                value={newEvent.title}
                onChange={(e) => setNewEvent({ ...newEvent, title: e.target.value })}
                placeholder="  "
              />
            </Form.Group>
            <Form.Group controlId="formEventStart">
              <Form.Label> </Form.Label>
              <Form.Control
                type="datetime-local"
                value={moment(newEvent.start).format('YYYY-MM-DDTHH:mm')}
                onChange={(e) => setNewEvent({ ...newEvent, start: new Date(e.target.value) })}
              />
            </Form.Group>
            <Form.Group controlId="formEventEnd">
              <Form.Label> </Form.Label>
              <Form.Control
                type="datetime-local"
                value={moment(newEvent.end).format('YYYY-MM-DDTHH:mm')}
                onChange={(e) => setNewEvent({ ...newEvent, end: new Date(e.target.value) })}
              />
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowAddModal(false)}>
            
          </Button>
          <Button variant="primary" onClick={handleAddEvent}>
            
          </Button>
        </Modal.Footer>
      </Modal>

      {/*      */}
      <Modal show={showDeleteModal} onHide={() => setShowDeleteModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title> </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>     ?</p>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowDeleteModal(false)}>
            
          </Button>
          <Button variant="danger" onClick={handleDeleteEvent}>
            
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
};

export default MyCalendar;
